@charset "utf-8";

@import "../../../static/sass/vendor/sassCore/base";

.ui-header {
  position: relative;
  height: 45px;
  line-height: 45px;
  text-align: center;
  background: #302f2f;
  color: #fff;
  .headerline {
    font-size: 18px;
    line-height: 45px;
  }
  .button-bulletin {
    position: absolute;
    top: 12px;
    right: 10px;
    height: 22px;
    line-height: 22px;
    padding: 0 10px;
    border: 1px solid #fff;
    color: #fff;
    @include border-radius(3px);
  }
}



