@charset "utf-8";

@import "../../../static/sass/vendor/sassCore/base";

.ui-play-list{
	padding: 10px;
	position:relative;
	@include display-flex();
	@include align-items(center);
	background:#fff;
	border-bottom:1px solid #ebebeb;
	.list-left{
		min-width: 20px;
		padding-right: 10px;
	}
	.list-mid{
		@include flex(1);
		@include display-flex();
		.avatar{
			width:40px;
			height:40px;
			overflow:hidden;
			border-radius:50%;
			border:2px solid #ffb3b7;
			img{
				width:100%;
				height:100%;
			}
		}
		.list-text{
			padding-left:10px;
			text-align:left;
			.play-message{
				font-size: 12px;
				color: #aaa;
			}
		}
	}
	.list-rank{
		padding: 0 10px;
	}
	.rank-item{
		display: inline-block;
		min-width: 16px;
		color: #d04646;
		text-align: center;
		vertical-align: middle;
	}
	.waiver{		
		padding:5px 8px;
		border:1px solid #C01E2F;
		color:#C01E2F;
		@include border-radius(3px);		
	}
}
.ui-play-list-number-1,
.ui-play-list-number-2,
.ui-play-list-number-3 {
	.rank-item {
		height: 16px;
		line-height: 16px;
		font-size: 12px;		
		background: #dcca80;
		color: #fff;
		@include border-radius(50%);
		
	}
}
.ui-play-list-number-2 {
	.rank-item {
		background: #bfbfbf
	}
}
.ui-play-list-number-3 {
	.rank-item {
		background: #ae635b
	}
}
.ui-play-list-gave{
	background:#f5f5f5;
	.list-left,
	.player-nick,
	.waiver {
		color:#aaa;
		border: none;
	}
	
}



